body {
    font-family: 'Jua', sans-serif;
}










header {
    position: fixed;
    top: 0; left: 0;
    width: 100%; height: 73px;
    border-bottom: 1px solid rgba(0,0,0,.08);
    box-sizing: border-box;
    background: #49494950;
    color: white;
}
header h1 {
    position: relative;
    font-size: 30px;
    margin: 20px; margin-left: 50px;
    display: inline-block;
}
header h1 img {
    width: 40px;
    position: absolute;
    top: -5px; left: 0;
}
header h1 span {
    padding-left: 50px;
}
header ul {
    position: absolute;
    top: 20px; right: 50px;
    display: inline-block;
}
header ul li {
    display: inline-block;
}
header ul li a {
    font-size: 16px;
    padding: 30px;
}
header ul li a:hover {
    color: seagreen;
}










main {
    margin: 0; padding: 0;
    margin-top: 73px;
}
section {
    width: 100%; height: 700px;
    color: white;
    text-align: center;
}
section h2 {
    padding-top: 40px;
    font-size: 50px;
}
section .poster {
    width: 800px; height: 550px;
    background: rgb(177, 158, 158);
    margin: 0 auto;
    overflow: hidden;
}
section .poster > div {
    margin-top: 25px;
    display: inline-block;
    width: 50%; height: 90%;
    background: tomato;
}
section .poster .option {
    float: left;
}
section .poster .option1 {
    border-right: 1px solid #fff;
}
.section0 {
    background: tomato;
}
.section1 {
    background: brown;
}
.section2 {
    background: burlywood;
}
.section3 {
    background: cadetblue;
}









footer {
    background: turquoise;
    width: 100%; height: 550px;
}